import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import GitHubIcon from '@material-ui/icons/GitHub';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import WorkIcon from '@material-ui/icons/Work';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import WebIcon from '@material-ui/icons/Web';
import './sidebar-styles.css';

const useStyles = makeStyles({
  list: {
    width: 250,
    justifyContent: 'space-between',
    display: 'flex',
    textAlign: 'center',
    flexDirection: 'column',
    height: '100%'
  },
  fullList: {
    width: 'auto'
  },
});

export default function SidebarMenu() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const icons = [<PersonPinIcon />,<WorkIcon />,<WebIcon />];
  const list = (anchor) => (
      <div
        className={clsx(classes.list, {
          [classes.fullList]: anchor === 'top' || anchor === 'bottom',
        })}
        role="presentation"
        onClick={toggleDrawer(anchor, false)}
        onKeyDown={toggleDrawer(anchor, false)}
      >
      <button className="btn-close-drawer" onClick={toggleDrawer('left', true)}>{<KeyboardBackspaceIcon />}</button>
      
      <div>          
          <h3>Oscar Islas</h3>
          <p>Full Stack Developer</p>
      </div>

      <List>
        {['Acerca de mí', 'Experiencia', 'Proyectos'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{icons[index]}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}        
      </List>

      <div className="social-networks">
        <h4>Sígueme</h4>
        <GitHubIcon />
      </div>
    </div>
  );

  return (
    <div>
      <button className="btn-open-drawer" onClick={toggleDrawer('left', true)}>{<ArrowRightAltIcon />}</button>
      <React.Fragment key={'left'}>
        <Drawer anchor={'left'} open={state['left']} onClose={toggleDrawer('left', false)}>
          {list('left')}
        </Drawer>
      </React.Fragment>      
    </div>
  );
}