import React from 'react';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Perfil from '../../images/perfil2.jpg';
import LinearProgress from '@material-ui/core/LinearProgress';
import './aboutme-styles.css';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

export default function AboutMe(){
    const classes = useStyles();
    const [completed, setCompleted] = React.useState(0);

    React.useEffect(() => {
        function progress() {
          setCompleted((oldCompleted) => {
            if (oldCompleted === 100) {
              return 0;
            }
            const diff = Math.random() * 10;
            return Math.min(oldCompleted + diff, 100);
          });
        }
    
        const timer = setInterval(progress, 500);
        return () => {
          clearInterval(timer);
        };
      }, []);

    return (
        <Container maxWidth="lg" className="about-me-section">
            <Grid container spacing={3}>
                <Grid item xs={12} md={8} className="about-me-col">
                    <span className="no-section">01</span><span className="separator-section"> | </span><h3>Acerca de mí</h3>
                    <p>
                        Soy Óscar Islas Reyes, actualmente soy <b>lead instructor</b> del curso <b>"Desarrollo Web Full-Stack y Ciencias de la computación"</b> en <a href="https://www.academlo.com/">Academlo</a>. 
                        <br />
                        Tengo 5 años de experiencia como desarrollador web full stack, desarrollando páginas y aplicaciones web Angular, React, Node, PHP y cuento con alrededor de 1 año de experiencia desarrollando aplicaciones móviles hibridas con React Native.                    
                    </p>
                    <div className="skills">
                        <div className="skill-item">
                            <h4>Javascript (ES6)</h4>
                            <LinearProgress variant="determinate" value={90} />
                        </div>
                        <div className="skill-item">
                            <h4>React JS</h4>
                            <LinearProgress variant="determinate" value={85} />
                        </div>
                        <div className="skill-item">
                            <h4>Angular 8</h4>
                            <LinearProgress variant="determinate" value={80} />
                        </div>
                        <div className="skill-item">
                            <h4>React Native</h4>
                            <LinearProgress variant="determinate" value={90} />
                        </div>
                        <div className="skill-item">
                            <h4>Node JS (Express)</h4>
                            <LinearProgress variant="determinate" value={90} />
                        </div>
                    </div>
                </Grid>
                <Grid item xs={12} md={4} className="img-col">
                    <img src={Perfil} alt="Foto perfil" className="img-perfil" />
                </Grid>
                
            </Grid>
        </Container>
    );
}