import React from 'react';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import developerClipArt from '../../images/developer.png';
import './experience-styles.css';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

export default function Skills(){

    const classes = useStyles();

    return (
        <Container maxWidth="lg" className="experience-section">
            <span className="no-section">02</span><span className="separator-section"> | </span><h3>Experiencia</h3>
            <Grid container spacing={3}>
                <Grid item xs={12} md={8} className="about-me-col">
                    <ol class="list">
                        <li class="item">
                            <h2 class="headline">Moce Ingeniería</h2><span>Soporte técnico, administración y seguridad de redes.</span>
                        </li>
                        <li class="item">
                            <h2 class="headline">Freelance</h2><span>Desarrollo de sitios web con Wordpress, Angular y PHP (Codeigniter).</span>
                        </li>
                        <li class="item">
                            <h2 class="headline">SEMPLICE</h2><span>Full Stack Web - Desarrollo de aplicaciones web con Angular, React, creación de API con Node y Express y despliegue de aplicaciones con AWS EC2, y ejecución de servicios con AWS Lambda.</span>
                        </li>
                        <li class="item">
                            <h2 class="headline">Academlo</h2><span>Lead Instructor del curso "Desarrollo Web Full-Stack y Ciencias de la computación".</span>
                        </li>
                    </ol>
                </Grid>
                <Grid item xs={12} md={4}>
                    <img src={developerClipArt} alt="clip-art" className="img-clipart" />
                </Grid>
            </Grid>
        </Container>
    )
}